import React, { useState } from "react";
import { Base64 } from "js-base64";
import { TextField, Button, Link } from "@mui/material";
import FormsApi from "../../API/api";
import user from "..//..//user.config";
import { Navigate, useNavigate } from "react-router-dom";
import "../Admin/admin.css";
import { Exam } from "../Lecturer/Exam";

export const LoginAdmin = () => {
  const navigate = useNavigate();
  const [submit, setSubmit] = useState(false);
  const form_submit = async (e) => {
    e.preventDefault();
    setSubmit(true);
    const fd = new FormData(e.target);
    let fobj = {};
    fd.forEach((value, key) => {
      fobj[key] = value;
    });

    const api = new FormsApi();
    const res = await api.post("/admin", fobj);

    if (res === "Error") {
      setSubmit(false);
      return;
    }
    if (res.status === false) {
      setSubmit(false);
    } else {
      const data = Base64.encode(JSON.stringify(res.user));
      localStorage.setItem("token", data);
      setSubmit(false);
    }
    navigate("/exam");
  };
  if (user) return <Exam />;
  return (
    <div className="adminRegister">
      <div
        style={{
          margin: "20px 0px 0px 70px",
          color: "blue",
          fontSize: "1.5rem",
        }}
      >
        <p>Login to your account</p>
      </div>
      <div className="registerActions" style={{ marginLeft: "70px" }}></div>
      <form onSubmit={form_submit}>
        <div style={{ margin: "20px 0px 20px 70px" }}>
          <TextField
            id="outlined-basic"
            label="Email"
            name="email"
            required
            type="email"
            style={{ width: "280px" }}
            variant="outlined"
          />
        </div>
        <div style={{ marginLeft: "70px" }}>
          <TextField
            id="outlined-basic"
            label="Password"
            name="password"
            required
            type="password"
            style={{ width: "280px" }}
            variant="outlined"
          />
        </div>
        <Button
          variant="contained"
          style={{ margin: "10px 0px 10px 70px" }}
          type="submit"
        >
          Login
        </Button>
      </form>
      <div className="have-an-account" style={{ margin: "0px 0px 10px 70px" }}>
        <p style={{ marginBottom: "5%" }}>
          Don't have an account?
          <Link href="/signup">Signup</Link>
        </p>
      </div>
    </div>
  );
};
