import axios from "axios";

const url = "http://localhost:5009/api/v6";

export default class FormsApi {
  //post method
  async post(i, data) {
    try {
      const res = await axios.post(`${url}${i}`, data);
      return res.data;
    } catch (error) {
      return "Error";
    }
  }

  //get method
  async get(i) {
    try {
      const res = await axios.get(`${url}${i}`);
      return res.data;
    } catch (error) {
      return "Error";
    }
  }
}
