import React, { useState } from "react";
import { Base64 } from "js-base64";
import { TextField, Button, Link } from "@mui/material";
import FormsApi from "../../API/api";
import user from "../../user.config";
import { useNavigate } from "react-router-dom";
import { useEffect } from "react";
import "../Admin/admin.css";

export const Register = () => {
  // const [state, setState] = useState(false);
  const navigate = useNavigate();
  const [submit, setSubmit] = useState(false);
  const form_submit = async (e) => {
    e.preventDefault();
    setSubmit(true);
    const fd = new FormData(e.target);
    let fobj = {};
    fd.forEach((value, key) => {
      fobj[key] = value;
    });

    const api = new FormsApi();
    const res = await api.post("/newadmin", fobj);

    if (res === "Error") {
      setSubmit(false);
      return;
    }

    if (res.status === false) {
      if (res.data === "Admin") {
        setSubmit(false);
        alert("Internal Error");
        return;
      } else {
        setSubmit(false);
        alert("Email taken");

        return;
      }
    } else {
      const data = Base64.encode(JSON.stringify({ ...res.result }));
      localStorage.setItem("token", data);
      setSubmit(true);
      navigate("/");
      alert("Data has been sent");
    }
  };
  useEffect(() => {
    if (user) {
      navigate(-1);
    }
  }, []);

  return (
    <section>
      <div className="adminRegister">
        <div
          style={{
            margin: "20px 0px 0px 70px",
            color: "blue",
            fontSize: "1.5rem",
          }}
        >
          <p>Create An Account</p>
        </div>
        <form onSubmit={form_submit}>
          <div className="registerActions" style={{ marginLeft: "70px" }}>
            <TextField
              id="outlined-basic"
              style={{ width: "280px" }}
              label="Username"
              variant="outlined"
              name="username"
            />
          </div>
          <div style={{ margin: "20px 0px 20px 70px" }}>
            <TextField
              id="outlined-basic"
              label="Email"
              style={{ width: "280px" }}
              variant="outlined"
              name="email"
              type="email"
            />
          </div>
          <div style={{ marginLeft: "70px" }}>
            <TextField
              id="outlined-basic"
              label="Password"
              style={{ width: "280px" }}
              variant="outlined"
              name="password"
              type="password"
            />
          </div>
          <Button
            variant="contained"
            style={{ margin: "10px 0px 10px 70px" }}
            value=" "
            type="submit"
          >
            Create
          </Button>
        </form>
        <div
          className="have-an-account"
          style={{ margin: "0px 0px 10px 70px" }}
        >
          <p style={{ marginBottom: "5%" }}>
            Have an account?
            <Link href="/">Login</Link>
          </p>
        </div>
      </div>
    </section>
  );
};
