import React, { useState } from "react";
import { TextField, Button } from "@mui/material";
import "../Admin/admin.css";
import FormsApi from "../../API/api";

export const Student = () => {
  const [submit, setSubmit] = useState(false);
  const form_submit = async (e) => {
    e.preventDefault();
    setSubmit(true);
    const fd = new FormData(e.target);
    let fobj = {};
    fd.forEach((value, key) => {
      fobj[key] = value;
    });

    const api = new FormsApi();
    const res = await api.post("/newstudent", fobj);

    if (res === "Error") {
      setSubmit(false);
      return;
    }

    if (res.status === false) {
      if (res.data === "student") {
        setSubmit(false);
        alert("Internal Error");
        return;
      } else {
        setSubmit(false);
        alert("Student exists");

        return;
      }
    } else {
      alert("Data has been sent");
    }
  };

  return (
    <div className="adminRegister-student">
      <div
        style={{
          margin: "20px 0px 0px 70px",
          color: "blue",
          fontSize: "1.5rem",
        }}
      >
        <p>Register A Student</p>
      </div>
      <div className="registerActions" style={{ marginLeft: "70px" }}></div>
      <form onSubmit={form_submit}>
        <div style={{ margin: "20px 0px 20px 70px" }}>
          <TextField
            id="outlined-basic"
            label="Name"
            style={{ width: "280px" }}
            variant="outlined"
            name="name"
          />
        </div>
        <div style={{ marginLeft: "70px" }}>
          <TextField
            id="outlined-basic"
            label="Faculty"
            style={{ width: "280px" }}
            variant="outlined"
            name="faculty"
          />
        </div>
        <div style={{ margin: "20px 0px 20px 70px" }}>
          <TextField
            id="outlined-basic"
            label="Department"
            style={{ width: "280px" }}
            variant="outlined"
            name="department"
          />
        </div>
        <div style={{ marginLeft: "70px" }}>
          <TextField
            id="outlined-basic"
            label="Registration Number"
            style={{ width: "280px" }}
            variant="outlined"
            name="reg_number"
          />
        </div>
        <div style={{ margin: "20px 20px 20px 70px" }}>
          <TextField
            id="outlined-basic"
            label="University Email"
            style={{ width: "280px" }}
            variant="outlined"
            name="uni_email"
          />
        </div>
        <Button
          variant="contained"
          type="submit"
          style={{ margin: "10px 0px 0px 70px" }}
        >
          Register
        </Button>
      </form>
    </div>
  );
};
